import Vue from 'vue'
import Router from 'vue-router'
import iView from 'iview'
import VCharts from 'v-charts'
import 'v-charts/lib/style.css';

Vue.use(VCharts)
Vue.use(iView)
Vue.use(Router)

export default new Router({
  routes: [
    {name: 'login', path: '/login', component: () => import('../components/login.vue') },
    {
      name: 'home', path: '/', component:() => import('../components/home.vue'),
      children: [
        {
          name: 'main',
          path: 'main',
          component:() => import('../components/main.vue'),
          meta:{
              requireLogin:true
          }
        },
        {
          name: 'user_info',
          path: 'user_info',
          component:() => import('../components/user_info.vue'),
          meta:{
              requireLogin:true
          }
        },
        {
          name: 'loginlog',
          path: 'loginlog',
          component:() => import('../components/loginlog.vue'),
          meta:{
              requireLogin:true
          }
        },
        {
          name:'capital_change',
          path:'capital_change',
          component:() => import('../components/capital_change.vue'),
          meta:{
             requireLogin:true
          }
        },
        {
          name: 'payment',
          path: 'payment',
          component:() => import('../components/payment.vue'),
          meta:{
              requireLogin:true
          }
        },
        {
          name: 'withdraw',
          path: 'withdraw',
          component:() => import('../components/withdraw.vue'),
          meta:{
              requireLogin:true
          }
        },
        {
          name: 'settlement_record',
          path: 'settlement_record',
          component:() => import('../components/settlement_record.vue'),
          meta:{
              requireLogin:true
          }
        },
        {
          name:'settlement_account',
          path:'settlement_account',
          component:() => import('../components/settlement_account.vue'),
          meta:{
              requireLogin:true
          }
        },
        {
          name:'order_record',
          path:'order_record',
          component:() => import('../components/order_record.vue'),
          meta:{
            requireLogin:true
          }
        },
        {
          name:'statistics',
          path:'statistics',
          component:() => import('../components/statistics.vue'),
          meta:{
            requireLogin:true
          }
        },
        {
          name:'my_agent',
          path:'my_agent',
          component:() => import('../components/my_agent.vue'),
          meta:{
            requireLogin:true
          }
        },
        {
          name:'profit_statistics',
          path:'profit_statistics',
          component:() => import('../components/profit_statistics.vue'),
          meta:{
            requireLogin:true
          }
        },
        {
          name:'change_password',
          path:'change_password',
          component:() => import('../components/change_password.vue'),
          meta:{
            requireLogin:true
          }
        }
      ],
      redirect: {path:'main'},
    }
  ]
})



